@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #f6ede4;
}

.App {
  text-align: center;
  min-width: 600px;
  font-family: 'Amatic SC', cursive;
}

.hero {
  opacity: 0;
  position: relative;
  background-image: url(./res/ui/Backdrop_1.svg);
  height: 99vh;
  min-height: 600px;
} 

.hero.animated {
  animation: fade-in 0.5s 0.1s forwards;
}

.logo {
  opacity: 0;
  position: absolute;
  left: calc(50% - 150px);
  top: calc(50% - 250px);
  width: 300px;
  height: 300px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 50px #000;
  transition: left 0.5s;
}

.logo.animated {
  animation: pop-in 1s 0.1s forwards;
}

#contacts-about {
  position: relative;
  box-shadow: 0 -10px 50px #555;
  padding-bottom: 50px;
  text-align: center;
}

.pfp-container {
  display: flex;
  justify-content: center;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  margin: 50px 50px 50px 50px;
  box-shadow: 0 0 50px #555;
  overflow: hidden;
}

.pfp {
  flex-shrink: 0;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
}

.contact-form {
  position: relative;
  width: 500px;
  height: 400px;
  margin: 50px 50px 50px 50px;
  padding: 10px 0 10px 0;
  border-radius: 35px;
  box-shadow: 0 -10px 50px #555;
}

.description.subtitle {
  font-weight: 100;
  font-family: 'Gloock', serif;
}

.description {
  margin: 10px 0 0 0;
  padding: 0 40px 0 40px;
  font-size: 2em;
}

.contact-section {
  display: flex;
  flex-wrap: wrap;
  width: 200px;
  margin: 0 0 0 50px;
  padding: 40px 0 0 0;
  justify-content: center;
}

.contact-details {
  font-weight: bold;
  width: 150px;
  margin: 0.25em 0 0.25em 0;
  text-align: left;
  font-size: 1.5em;
}

#footer {
  position: relative;
  height: 100px;
  box-shadow: 0 -10px 50px #555;
  text-align: center;
}

@keyframes fade-in {
  0% {opacity: 0;}
  100%{opacity: 1;}
}

@keyframes pop-in {
  0% {opacity: 0; transform: translateY(80px)}
  40% {opacity: 1; transform: translateY(-30px)}
  100%{opacity: 1; transform: translateY(0px)}
}